.b-preloader-2 {
	$anim_dur:	7s;

	position: fixed;
	top: 0;
	left: 0;
	z-index: $z_i-b-preloader;

	display: flex;
	align-items: center;
	justify-content: center;

	width: 100%;
	height: 100%;

	padding: 20px;

	background-color: $white;

	&__inner {}

	&__logo {
		display: flex; // to collapse margins inside
		flex-direction: column;
		margin-bottom: 45px;

		&_last {
			margin-bottom: 0; }

		&::after {
			content: 'Resumedone';

			display: block;

			margin: -3px 0;

			color: $black;
			font-size: 21px;
			line-height: 1;
			font-weight: 700;
			letter-spacing: -.04em; } }

	&__list {
		position: relative; // for &::before and &::after
		display: flex; // to collapse margins inside
		flex-direction: column;

		height: 154px;
		overflow: hidden;

		&::before,
		&::after {
			content: '';

			position: absolute;
			left: 0;
			z-index: 1;

			width: 100%;
			height: 48px;

			pointer-events: none; }

		&::before {
			top: 0;
			background-image: linear-gradient( to bottom, rgba( $white, 1 ), rgba( $white, 0 ) ); }

		&::after {
			bottom: 0;
			background-image: linear-gradient( to top, rgba( $white, 1 ), rgba( $white, 0 ) ); } }

	&__list-inner {
		position: relative; // for animation
		margin: -10px;
		animation: preloader2ListInner $anim_dur linear forwards paused; }

	&_running .b-preloader-2__list-inner {
		animation-play-state: running; }

	&__list-item {
		padding: 10px; }

	&__box {
		position: relative; // for &::before and &::after
		padding: 14px 0 14px 68px;

		&::before {
			content: '';

			position: absolute;
			top: 0;
			left: 0;

			width: 38px;
			height: 38px;

			border-radius: 50%;
			border: 2px solid $brand-primary;
			background-color: rgba( $brand-primary, 0 );

			animation: preloader2BoxBefore $anim_dur linear forwards paused; }

		.s-tick {
			position: absolute;
			top: 13px;
			left: 11px;

			opacity: 1;

			height: 12px;

			path {
				fill: $brand-primary;
				animation: preloader2BoxAfter $anim_dur linear forwards paused; } } }

	&__list-item_1 .b-preloader-2__box::before,
	&__list-item_1 .b-preloader-2__box .s-tick path {
		animation-delay: -$anim_dur/5*1; }

	&__list-item_2 .b-preloader-2__box::before,
	&__list-item_2 .b-preloader-2__box .s-tick path {
		animation-delay: 0s; }

	&__list-item_3 .b-preloader-2__box::before,
	&__list-item_3 .b-preloader-2__box .s-tick path {
		animation-delay: $anim_dur/5*1; }

	&__list-item_4 .b-preloader-2__box::before,
	&__list-item_4 .b-preloader-2__box .s-tick path {
		animation-delay: $anim_dur/5*2; }

	&__list-item_5 .b-preloader-2__box::before,
	&__list-item_5 .b-preloader-2__box .s-tick path {
		animation-delay: $anim_dur/5*3; }

	&__list-item_6 .b-preloader-2__box::before {
		animation: preloader2BoxBeforeLast $anim_dur linear $anim_dur/5*4 forwards paused; }

	&__list-item_6 .b-preloader-2__box .s-tick path {
		animation: preloader2BoxAfterLast $anim_dur linear $anim_dur/5*4 forwards paused; }

	&_running .b-preloader-2__box::before,
	&_running .b-preloader-2__box .s-tick path {
		animation-play-state: running; }

	&__text {
		margin: -7px 0;

		color: $black;
		font-size: 14px;
		line-height: 24px;
		font-weight: 500;
		letter-spacing: .06em; }

	@keyframes preloader2BoxBefore {

		from {
			background-color: rgba( $brand-primary, 0 ); }

		16% {
			background-color: rgba( $brand-primary, 0 ); }

		20% {
			background-color: rgba( $brand-primary, .6 ); }

		36% {
			background-color: rgba( $brand-primary, .6 ); }

		40% {
			background-color: rgba( $brand-primary, .4 ); }

		to {
			background-color: rgba( $brand-primary, .4 ); } }

	@keyframes preloader2BoxBeforeLast {

		from {
			background-color: rgba( $brand-primary, 0 ); }

		16% {
			background-color: rgba( $brand-primary, 0 ); }

		20% {
			background-color: rgba( $brand-primary, .6 ); }

		to {
			background-color: rgba( $brand-primary, .6 ); } }

	@keyframes preloader2BoxAfter {

		from {
			opacity: 1;
			fill: $brand-primary; }

		16% {
			fill: $brand-primary; }

		20% {
			fill: $white; }

		36% {
			opacity: 1;
			fill: $white; }

		40% {
			opacity: 0; }

		to {
			opacity: 0; } }

	@keyframes preloader2BoxAfterLast {

		from {
			opacity: 1;
			fill: $brand-primary; }

		16% {
			fill: $brand-primary; }

		20% {
			fill: $white; }

		to {
			opacity: 1;
			fill: $white; } }

	@keyframes preloader2ListInner {

		from {
			top: 58px; }

		16% {
			top: 58px; }

		20% {
			top: 0; }

		36% {
			top: 0; }

		40% {
			top: -58px; }

		56% {
			top: -58px; }

		60% {
			top: -116px; }

		76% {
			top: -116px; }

		80% {
			top: -174px; }

		96% {
			top: -174px; }

		to {
			top: -232px; } } }
