.c-slick-examples {

	@extend .c-slick;

	.slick-list {
		height: calc( 100% + 240px );
		margin: -140px 0; }

	&_style_2 .slick-list {
		height: calc( 100% + 280px ); }

	.slick-track {
		display: flex;
		align-items: center;

		height: 100%;

		padding: 140px 0; }

	.slick-slide {
		position: relative;
		height: 95%;

		&.slick-center {
			z-index: 1; } }

	.slick-arrow {
		position: absolute;
		top: 50%;
		z-index: 9;

		width: 52px;
		height: 52px;

		border-radius: 50%;

		background-color: #0099da;

		font-size: 0;

		transform: translateY( -50% );

		@extend .i-arrow-7;

		&:hover {
			background-color: #0087c1; }

		&::after {
			position: absolute;
			top: 18px;

			color: $white;
			font-size: 14px;

			transition: all .2s; } }

	&_style_2 .slick-arrow {
		top: calc( 50% - 20px );

		background-color: #0099da;

		transition: background-color .2s;

		&:hover {
			background-color: #0087c1; }

		&::after {
			font-size: 16px; } }

	&_theme_2 .slick-arrow {
		background-color: #f37101;

		&:hover {
			background-color: #de6902; } }

	.slick-prev {
		left: 20px;

		&::after {
			left: 20px;
			transform: rotateY( 180deg ); }

		&:hover::after {
			left: 17px; } }

	&_style_2 .slick-prev {
		left: 35px; }

	.slick-next {
		right: 20px;

		&::after {
			right: 20px; }

		&:hover::after {
			right: 17px; } }

	&_style_2 .slick-next {
		right: 35px; } }
